@import '../../global.scss';
.skills{
    background-color: crimson;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    
    

    .arrow{
        height: 50px;
        position: absolute;
        cursor: pointer;
        font-size: 70px;

        @include mobile{
            display: none;
        }
        
        &.lef{
            left: 100px;
            color: white;
            transform: rotateY(180deg);
            
        }

        &.righ{
            right: 100px;
            color: white;
        }

        
    }

    

    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out;

        @include mobile{
            height: 100vh;
            flex-direction: column;
            justify-content: center;
        }

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;
            
        }
        .item{
            width: 700px;
            height: 100%;
            background-color: white;
            border-radius: 20px;
            display: flex;
            
            justify-content: center;
            align-items: center;
            @include mobile{
                width: 80%;
                height: 150px;
                margin: 10px 0;
               
            }

            .left{
                flex: 4;
                height: 80%;
                display: flex;
                justify-content: center;
                align-items: center;
            

            .leftContainer{
                width: 90%;
                height: 70%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                @include mobile{
                    width: 100%;
                    height: 100%;
                    
                }
            }

            .imgContainer{
                width: 40px;
                height: 40px;
                border-radius: 50%;
                background-color: rgb(238, 155, 125);
                display: flex;
                align-items: center;
                justify-content: center;
            }

            img{
                width: 75px;
                @include mobile{
                    width: 30px;
                    
                }
            }
                
            }

            h2{
                font-size: 20px;
                @include mobile{
                    font-size: 15px;
                    
                }

                
            }
            p{
                font-size: 13px;
                @include mobile{
                    display: none;
                    
                }
            }
            span{
                font-size: 12px;
                font-weight: 600;
                text-decoration: underline;
                cursor: pointer;
            }

            .right{
                flex: 8;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                overflow: hidden;

                img{
                    width: 300px;
                    transform: rotate(-10deg);
                    
                }
                
            }

        }

    }

    
}